}
#ifm-importer-app hr {
  margin-top: 10px;
  margin-bottom: 10px;
  clear: both;
}
#ifm-importer-app summary {
  font-size: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
}
#ifm-importer-app summary:hover {
  cursor: pointer;
}
#ifm-importer-app tr th,
#ifm-importer-app tr td,
#ifm-importer-app tfoot tr {
  min-width: 130px;
  border: 1px solid #333333;
  text-align: left;
  border-collapse: collapse;
  padding: 3px;
}
#ifm-importer-app tfoot tr td {
  colspan: "100%";
}
#ifm-importer-app details ul {
  height: 300px;
  overflow: scroll;
  border: 1px solid #e3e3e3;
  background: #f9f9f9;
  width: 270px;
  padding: 10px;
  border-radius: 5px;
}
#ifm-importer-app summary:focus {
  outline-style: none;
}
#ifm-importer-app .wp-core-ui select {
  height: 30px;
}
#ifm-importer-app button:hover {
  cursor: pointer;
}
#ifm-importer-app h4 {
  margin: 0;
  font-size: 17px;
  font-weight: 400;
}
#ifm-importer-app .ifm-input-group {
  display: flex;
  align-items: flex-end;
}
#ifm-importer-app .ifm-input-group > select,
#ifm-importer-app .ifm-input-group .v-select {
  min-width: 25%;
  margin-right: 5px;
}
#ifm-importer-app .ifm-input-group > select .dropdown-menu,
#ifm-importer-app .ifm-input-group .v-select .dropdown-menu {
  height: auto;
  overflow: scroll;
  width: 100%;
  background: #f9f9f9;
  border: none;
  padding: 0;
}
#ifm-importer-app .ifm-input-group > select .dropdown-menu li,
#ifm-importer-app .ifm-input-group .v-select .dropdown-menu li {
  margin: 0;
  padding-top: 2.5px;
  padding-bottom: 2.5px;
}
#ifm-importer-app .ifm-input-group > select .highlight,
#ifm-importer-app .ifm-input-group .v-select .highlight {
  background: #007cba;
}
#ifm-importer-app .ifm-input-group > select .highlight > a,
#ifm-importer-app .ifm-input-group .v-select .highlight > a {
  background: #007cba;
}
#ifm-importer-app .vs__selected-options,
#ifm-importer-app .selected-tag {
  padding: 0;
}
#ifm-importer-app .ifm-input-group button {
  width: 30px;
  flex-grow: 0;
}
#ifm-importer-app .ifm-input-wrapper {
  display: flex;
  flex-direction: column;
  margin-right: 5px;
  min-width: 25%;
  flex-grow: 1;
  margin-top: 10px;
  padding-bottom: 5px;
}
#ifm-importer-app .ifm-input-wrapper label {
  margin-bottom: 5px;
}
#ifm-importer-app .ifm-input-wrapper input,
#ifm-importer-app .ifm-input-wrapper select,
#ifm-importer-app .ifm-input-wrapper .v-select {
  width: 100%;
}
#ifm-importer-app .ifm-input-wrapper:last-child {
  margin-right: 0;
}
#ifm-importer-app .v-select .dropdown-toggle {
  height: 30px;
  background-color: white !important;
}
#ifm-importer-app .v-select .dropdown-toggle {
  background-color: white;
  font-size: 14px;
  line-height: 2;
  color: #7e8993;
  border-color: #7e8993;
  box-shadow: none;
  border-radius: 3px;
  padding: 0 0 0 8px;
  min-height: 30px;
  -webkit-appearance: none;
  background: #fff url(data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2220%22%20height%3D%2â€¦%205-5%202%201-7%207-7-7%202-1z%22%20fill%3D%22%23555%22%2F%3E%3C%2Fsvg%3E) no-repeat right 5px top 55%;
  background-size: 16px 16px;
  cursor: pointer;
  vertical-align: middle;
}
#ifm-importer-app .v-select input[type="search"],
#ifm-importer-app .v-select input[type="search"]:focus {
  margin: 0;
}
#ifm-importer-app .v-select .open-indicator:before {
  border-color: #333333;
  border-style: solid;
  border-width: 2px 2px 0 0;
  height: 8px;
  width: 8px;
}
#ifm-importer-app .v-select .selected-tag {
  margin: 0;
  height: 30px;
}
#ifm-importer-app .v-select .dropdown-toggle .clear {
  font-weight: 300;
  color: #333333;
  border: none;
  height: 27px;
}
#ifm-importer-app .ifm-table-wrapper {
  overflow: scroll;
  margin-bottom: 10px;
  padding-bottom: 12px;
}
#ifm-importer-app .csv-table {
  border-collapse: collapse;
  background: white;
}
#ifm-importer-app .ifm-table-wrapper table {
  table-layout: fixed;
}
#ifm-importer-app .ifm-table-wrapper td {
  word-wrap: break-word;
}
#ifm-importer-app .ifm-table-wrapper .cell-content {
  word-break: break-all;
}
#ifm-importer-app .import-fields {
  background: white;
}
#ifm-importer-app .import-fields,
#ifm-importer-app .import-fields-wrapper {
  position: relative;
}
#ifm-importer-app .import-fields-wrapper {
  overflow: scroll;
}
#ifm-importer-app .import-fields:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(241, 241, 241, 0);
  background: -moz-linear-gradient(top, rgba(241, 241, 241, 0) 0%, rgba(241, 241, 241, 0.5) 90%, #f1f1f1 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(241, 241, 241, 0)), color-stop(90%, rgba(241, 241, 241, 0.5)), color-stop(100%, #f1f1f1));
  background: -webkit-linear-gradient(top, rgba(241, 241, 241, 0) 0%, rgba(241, 241, 241, 0.5) 90%, #f1f1f1 100%);
  background: -o-linear-gradient(top, rgba(241, 241, 241, 0) 0%, rgba(241, 241, 241, 0.5) 90%, #f1f1f1 100%);
  background: -ms-linear-gradient(top, rgba(241, 241, 241, 0) 0%, rgba(241, 241, 241, 0.5) 90%, #f1f1f1 100%);
  background: linear-gradient(to bottom, rgba(241, 241, 241, 0) 0%, rgba(241, 241, 241, 0.5) 90%, #f1f1f1 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#0f1f1f', endColorstr='#0f1f1f', GradientType=0);
}
#ifm-importer-app .entry-length {
  width: 50px;
}
#ifm-importer-app .preview-details {
  font-size: 15px;
  margin-top: 4px;
  margin-bottom: 4px;
}
#ifm-importer-app .import-list {
  display: block;
  float: left;
  max-width: 90%;
  overflow: hidden;
  padding-right: 10px;
}
#ifm-importer-app .ifm-steps-and-vars {
  float: left;
  width: 60%;
  clear: both;
  align-items: baseline;
}
#ifm-importer-app .ifm-create-steps,
#ifm-importer-app .ifm-var-builder {
  float: left;
  margin-right: 5px;
  border: 1px solid #333333;
  background: white;
}
#ifm-importer-app .ifm-create-steps {
  width: 100%;
  margin-bottom: 10px;
}
#ifm-importer-app .ifm-steps-wrapper {
  display: flex;
  width: auto;
  flex-wrap: wrap;
}
#ifm-importer-app .ifm-define-action,
#ifm-importer-app .ifm-params {
  padding: 5px 10px;
  margin: 5px 0;
  display: flex;
  background: #f9f9f9;
  border: 1px solid #e3e3e3;
}
#ifm-importer-app .type-and-value {
  display: flex;
  flex-direction: row;
  padding: 5px;
  margin: 5px;
  margin-top: 0;
  margin-bottom: 0;
  border: 1px solid #e3e3e3;
  background: #f9f9f9;
}
#ifm-importer-app .type-and-value .ifm-input-wrapper {
  padding-bottom: 0;
  margin-top: 5px;
  background: #f9f9f9;
}
#ifm-importer-app .delete-map-row {
  margin-bottom: 5px;
}
#ifm-importer-app .ifm-define-action {
  display: block;
  padding: 10px;
}
#ifm-importer-app .ifm-entity-verb-wrapper {
  width: 100%;
  display: flex;
}
#ifm-importer-app .ifm-entity-verb-wrapper select:first-child {
  margin-right: 10px;
}
#ifm-importer-app .ifm-entity-verb-wrapper * {
  flex-grow: 1;
}
#ifm-importer-app .ifm-params {
  display: block;
  padding: 15px;
}
#ifm-importer-app .ifm-params button {
  height: 30px;
  width: 30px;
}
#ifm-importer-app .ifm-define-action > select {
  flex-grow: 1;
}
#ifm-importer-app .ifm-define-action .button-primary {
  margin-left: 5px;
}
#ifm-importer-app .ifm-create-steps .ifm-step summary {
  padding: 0;
  height: 30px;
  background: none;
}
#ifm-importer-app .ifm-create-steps .ifm-step .ifm-params.ifm-conditional summary {
  height: auto;
  font-size: 15px;
}
#ifm-importer-app .ifm-step-number {
  line-height: 30px;
}
#ifm-importer-app .ifm-summary-right {
  float: right;
  display: flex;
  align-items: center;
}
#ifm-importer-app .ifm-step-name {
  font-size: 14px;
  color: #333333;
  margin-right: 10px;
}
#ifm-importer-app .ifm-step {
  display: block;
  float: left;
  width: 100%;
  margin: 10px;
  margin-top: 0;
  padding: 10px;
  background: white;
  border: 1px solid #333333;
}
#ifm-importer-app .ifm-step .regular-text {
  width: 13em;
}
#ifm-importer-app .ifm-step .key-row,
#ifm-importer-app .ifm-step .key-value {
  display: flex;
}
#ifm-importer-app .ifm-step .button-primary.wide {
  width: 100%;
  margin-top: 10px;
}
#ifm-importer-app .ifm-step .single-meta {
  margin-top: 5px;
  margin-bottom: 5px;
  display: flex;
  flex-direction: column;
}
#ifm-importer-app .ifm-step .equals {
  height: 30px;
  font-size: 20px;
}
#ifm-importer-app .ifm-var-builder {
  width: 39%;
  float: right;
}
#ifm-importer-app .ifm-var-builder summary,
#ifm-importer-app .ifm-create-steps summary {
  background: white;
  margin: 0;
  padding: 20px;
}
#ifm-importer-app .raw-json pre {
  margin: 20px;
  margin-top: 0;
  padding: 20px;
  background: #f9f9f9;
  border: 1px solid #e3e3e3;
}
#ifm-importer-app .add-custom-value {
  margin-top: 10px;
}
#ifm-importer-app .ifm-params .button-primary {
  font-size: 10px;
  height: 20px;
  min-height: 20px;
  width: 20px;
  line-height: 0px;
  text-align: center;
  padding: 0;
}
#ifm-importer-app .custom-var-header .ifm-params {
  padding: 10px;
}
#ifm-importer-app .custom-var-header .ifm-input-wrapper {
  background: #f9f9f9;
  border: 1px solid #e3e3e3;
}
#ifm-importer-app .var-name {
  width: 100%;
}
#ifm-importer-app .custom-var-header label {
  margin-bottom: 5px;
}
#ifm-importer-app .custom-var-header .button-primary {
  position: absolute;
  right: 15px;
  top: 10px;
}
#ifm-importer-app .map-wrapper {
  background: #f9f9f9;
  border: 1px solid #e3e3e3;
  padding: 5px;
  padding: 10px 5px;
}
#ifm-importer-app .map-wrapper .delete-map-row {
  margin-bottom: 0;
}
#ifm-importer-app .map-wrapper .var-segment {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  flex-grow: 1;
}
#ifm-importer-app .map-wrapper .ifm-params {
  display: flex;
  align-items: flex-end;
  padding: 0;
  margin: 0;
  flex-grow: 1;
  border: none;
  margin-bottom: 3px;
}
#ifm-importer-app .map-wrapper .ifm-input-wrapper {
  padding: 0;
  margin-right: 5px;
  margin-top: 3px;
  border: none;
}
#ifm-importer-app .var-type-wrapper {
  margin-right: 10px;
}
#ifm-importer-app .var-value {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
}
#ifm-importer-app .var-input-label {
  margin-bottom: 10px;
}
#ifm-importer-app .ifm-custom-var h4 {
  margin: 10px 0;
}
#ifm-importer-app .ifm-custom-var {
  border: 1px solid #333333;
  margin: 5px;
  position: relative;
  margin: 10px;
  padding: 10px;
  margin-top: 0;
}
#ifm-importer-app .ifm-custom-var .ifm-custom-var-wrapper {
  background: white;
  border-bottom: none;
  position: relative;
  padding: 0;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
#ifm-importer-app .ifm-custom-var .ifm-custom-var-wrapper .content-left {
  flex-grow: 1;
}
